/*For IE*/
select::-ms-expand {
  display: none;
}

.checkbox--label {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10.6px;
  flex: 1 0 0;
  justify-content: flex-start;
}

input[type="checkbox"] {
  width: 21px;
  height: 21px;
  border: 0.15em solid var(--heading-color);
  border-radius: 0.15em;
  transform: translatey(-0.075em);
}

/* Contact End */

/* Footer Start */
/* Footer Starts */
.footer--container {
  display: flex;
  padding: 106.667px 85.333px;
  flex-direction: column;
  align-items: flex-start;
  background: var(--bg-shade);
  align-items: center;
  align-self: stretch;
}

.footer--link--container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-self: stretch;
}

.footer--items>ul {
  list-style: none;
  display: flex;
  align-items: flex-start;
  gap: 42.667px;
  text-decoration: none;
}

.footer--items ul>li>a {
  text-decoration: none;
}

.footer--social--icon>ul {
  list-style: none;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 16px;
  flex-shrink: 0;
  text-decoration: none;
}

.divider {
  margin: 106px 0 42.67px;
  height: 1.333px;
  width: 100%;
  background: rgba(40, 41, 56, 0.55);
}

.footer--content--container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-self: stretch;
}

.footer--content {
  color: var(--black);
  font-size: 18.667px;
  font-weight: 400;
  line-height: 28px;
}

.footer--items li,
.footer--items a,
.footer--social--icon li,
.footer--social--icon a {
  cursor: pointer;
}

.footer--items a:hover,
.footer--social--icon a:hover {
  opacity: 0.8;
  transition: opacity 0.2s ease-in-out;
}

.logo-left {
  text-align: left;
  /* Aligns content (the image) to the left */
}

.logo-left img {
  width: 200px;
  /* Set desired width */
  height: auto;
  /* Maintain aspect ratio */
  display: block;
  /* Removes any inline spacing */
  cursor: pointer;
}

/*tech trvel*/
.tech-reveal-layer {
  position: fixed;
  inset: 0;
  z-index: -1;

  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  gap: 0px;
  justify-items: center;
  align-items: center;

  background: #ffffff;
  color: #5e3bee;
  opacity: 0.4;
  font-size: 1.2rem;
  font-family:monospace;
  pointer-events: none;

  /* Spotlight effect */
  -webkit-mask-image: radial-gradient(circle 1.5rem at var(--x, -9999px) var(--y, -9999px),
      rgba(255, 255, 255, 1) 0%,
      rgba(255, 255, 255, 0.6) 60%,
      rgba(255, 255, 255, 0.2) 85%,
      transparent 100%);
  mask-image: radial-gradient(circle 120px at var(--x, -9999px) var(--y, -9999px),
      rgba(255, 255, 255, 1) 0%,
      rgba(255, 255, 255, 0.6) 60%,
      rgba(255, 243, 243, 0.2) 85%,
      transparent 100%);
  -webkit-mask-repeat: no-repeat;
  mask-repeat: no-repeat;
  -webkit-mask-composite: destination-in;
  mask-composite: intersect;
}

.tech-word {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  border: 1px solid rgba(0, 0, 0, 0.05);
  /* light grid lines */
  box-sizing: border-box;
  padding: 5px;
}

.tech-word.vertical {
  transform: rotate(-90deg);
  white-space: nowrap;
}

/* ================== Responsive Screens ================== */

/* Large Desktops */
@media only screen and (max-width: 1800px) {
  .hero--section--title {
    font-size: 68px;
    line-height: 70px;
  }

  .skills--section--title {
    font-size: 28px;
    line-height: 40px;
  }
}

/* Desktops */
@media only screen and (max-width: 1600px) {
  .skills--section--heading {
    font-size: 54px;
    line-height: 70px;
  }

  .skills--section--container {
    gap: 16px;
  }

  .skills--section--card {
    gap: 28px;
  }

  .skills--section--card--content {
    gap: 20px;
  }

  .skills--section--title {
    font-size: 23px;
    line-height: 30px;
  }

  .skills--section--description {
    font-size: 17.333px;
    line-height: 27px;
  }
}

/* Tablets and Small Laptops */
@media only screen and (max-width: 1200px) {
  .btn-outline-primary {
    display: none;
  }

  .hero--section {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    padding-bottom: 70px;
    text-align: center;
  }

  .hero--section--content {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .hero--img--wrapper {
    display: flex;
    justify-content: center;
    width: 100%;
    max-width: 300px;
    height: auto;
    margin-top: 20px;
  }

  .hero--img--wrapper img {
    width: 100%;
    height: auto;
    border-radius: 1px;
  }

  .skills--section--container,
  .about--section,
  .portfolio--section--container,
  .portfolio--container-box,
  .footer--link--container,
  .footer--items > ul,
  .footer--content--container {
    display: flex;
    flex-direction: column;
  }

  .skills--section,
  .portfolio--container,
  .hero--section--content,
  .hero--section--content--box {
    align-items: center;
  }

  .portfolio--container-box {
    gap: 30px;
  }

  .skills--section,
  .testimonial--section,
  .contact--section,
  .footer--container {
    gap: 20px;
    padding-top: 70px;
    padding-bottom: 70px;
  }

  .about--section {
    gap: 20px;
    padding-top: 0;
    padding-bottom: 0px;
  }

  .testimonial--section--card {
    padding: 25px;
  }

  .hero--section--title,
  .skills--section--heading,
  .sections--heading {
    font-size: 35px;
    line-height: 40px;
  }

  .hero--section--content--box {
    gap: 10px;
  }

  .contact--form--container {
    width: 100%;
  }

  .container {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  .footer--social--icon > ul,
  .footer--items > ul {
    padding: 0;
    align-items: center;
  }

  .divider {
    margin: 20px;
  }
}

/* Tablets and Small Devices */
@media only screen and (max-width: 768px) {
  .hero--section--title {
    font-size: 28px;
    line-height: 36px;
  }

  .skills--section--heading,
  .sections--heading {
    font-size: 28px;
    line-height: 36px;
    text-align: center;
  }

  .skills--section--description,
  .text-md {
    font-size: 14px;
    line-height: 22px;
  }

  .skills--section--container,
  .about--section {
    grid-template-columns: 1fr;
    text-align: center;
  }

  .about--section--img {
    justify-content: center;
  }

  .portfolio--section--img img {
    height: auto;
    width: 100%;
  }

  .contact--form--container {
    width: 90%;
    padding: 0 1rem;
  }

  .techstack--logo--item {
    min-width: 80px;
    height: 80px;
  }
}

/* Phones and Small Screens */
@media only screen and (max-width: 480px) {
  .hero--section {
    padding: 60px 20px;
  }

  .skills--section,
  .portfolio--section,
  .contact--section,
  .footer--container {
    padding: 40px 20px;
  }

  .hero--img--wrapper,
  .about--img--wrapper {
    width: 100%;
    height: auto;
  }

  .certifications--section--container {
    flex-direction: column;
    align-items: center;
  }

  .certifications--section--card,
  .skills--section--card {
    width: 100%;
    padding: 20px;
  }

  .navbar {
    padding: 10px 20px;
  }

  .footer--content,
  .footer--items ul {
    text-align: center;
  }
}


/* ================== End Responsive ================== */
